require('./scss/styles.scss');
import Swiper from 'swiper';
import {
    Thumbs,
    Grid,
    Navigation,
    Pagination,
    Autoplay,
    FreeMode,
    EffectCards,
    EffectFade,
    Controller
} from 'swiper/modules';
import debounce from "lodash/debounce";
import {ResponsiveSlider} from "./js/ResponsiveSlider";

Swiper.use([Thumbs, Navigation, Pagination, Autoplay, FreeMode, EffectCards, EffectFade, Controller]);

document.addEventListener("DOMContentLoaded", (event) => {


        const welcomeSlider = document.querySelector('.welcome-slider__swiper');
        if (welcomeSlider) {
            const navigation = document.querySelector('.welcome-slider__navigation');
            const nextButton = document.querySelector('.welcome-slider__next');
            const prevButton = document.querySelector('.welcome-slider__prev');

            const slider = new Swiper(".welcome-slider .swiper", {
                slidesPerView: 1,
                effect: "fade",
                loop: true,
                navigation: {
                    nextEl: '.welcome-slider__next',
                    prevEl: '.welcome-slider__prev',
                },
                pagination: {
                    el: '.welcome-slider__pagination',
                    type: 'fraction'
                },
                on: {
                    beforeSlideChangeStart: function (swiper) {
                        if (window.innerWidth >= 576) {
                            handleSlideAnimation.call(this, swiper);
                        }
                    },
                },
            });

            // Функция для анимации слайдов
            function handleSlideAnimation(swiper) {
                const prevSlide = this.slides[this.previousIndex];
                prevSlide.classList.add('still-visible');
                this.allowSlideNext = false;
                this.allowSlidePrev = false;
                navigation.classList.add('circle-animation');

                setTimeout(() => {
                    prevSlide.classList.remove('still-visible');
                    const currentSlide = this.slides[this.activeIndex];
                    currentSlide.classList.add('animate-back');

                    setTimeout(() => {
                        currentSlide.classList.remove('animate-back');
                        navigation.classList.remove('circle-animation');
                        this.allowSlideNext = true;
                        this.allowSlidePrev = true;
                    }, 800);
                }, 600);
            }

            // Навешиваем логику на кнопки навигации
            nextButton.addEventListener('click', function (e) {
                if (!slider.allowSlideNext) return; // Блокировка навигации во время анимации
                handleSlideAnimation.call(slider, slider);
            });

            prevButton.addEventListener('click', function (e) {
                if (!slider.allowSlidePrev) return; // Блокировка навигации во время анимации
                handleSlideAnimation.call(slider, slider);
            });
        }

        const header = document.querySelector('header'); // Находим элемент хедер
        if (header) {
            window.addEventListener('scroll', function () {
                if (window.scrollY > 100) { // Если прокручено более чем на 100px
                    header.classList.add('scrolled'); // Добавляем класс
                } else {
                    header.classList.remove('scrolled'); // Убираем класс
                }
            });
        }

        new ResponsiveSlider('.interiors').initSwipers({
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesPerView: "auto",
            spaceBetween: 20,
            navigation: {
                nextEl: '.interiors__prev',
                prevEl: '.interiors__next',
            },
            pagination: {
                el: '.interiors__pagination',
                type: 'fraction'
            },
            breakpoints: {
                601: {
                    spaceBetween: 24,
                },
                768: {
                    spaceBetween: 32,
                },
                992: {
                    spaceBetween: 36,
                    slidesOffsetBefore: 0,
                },
                1440: {
                    spaceBetween: 48,
                    slidesOffsetBefore: 0,
                },
            }
        }, true);


        new Swiper(".video-slider__slider.swiper", {
            slidesPerView: 1,
            navigation: {
                nextEl: '.video-slider__next',
                prevEl: '.video-slider__prev',
            },
            pagination: {
                el: '.video-slider__pagination', // Это можно оставить, если нужен индикатор
                type: 'custom', // Устанавливаем кастомную пагинацию
                renderCustom: function (swiper, current, total) {
                    document.querySelector('.video-slider__current-num').textContent = current <= 9 ? '0' + current : current;
                    document.querySelector('.video-slider__total-num').textContent = total <= 9 ? '0' + total  : total;
                }
            },
        });

        function scrollToElementWithHeaderOffset(element, header) {
            const headerHeight = document.querySelector('header').offsetHeight; // Высота хедера
            const elementPosition = element.getBoundingClientRect().top; // Позиция элемента относительно верхней границы окна
            const offsetPosition = elementPosition - headerHeight; // Позиция с учетом высоты хедера

            // Прокручиваем страницу на нужное расстояние
            window.scrollBy({
                top: offsetPosition,
                behavior: 'smooth'
            });
        }

        const tabsBlock = document.querySelectorAll('[data-tabs]');
        if (tabsBlock.length) {
            tabsBlock.forEach(tabsBlock => {
                const btns = tabsBlock.querySelectorAll('[data-tab]');
                const blocks = tabsBlock.querySelectorAll('[data-tab-block]');
                for (let btn of btns) {
                    btn.addEventListener('click', () => {
                        const id = parseInt(btn.getAttribute('data-tab'));
                        for (let block of blocks) {
                            if (parseInt(block.getAttribute('data-tab-block')) === id) {
                                block.setAttribute('data-active', '');
                                scrollToElementWithHeaderOffset(block);
                            } else {
                                block.removeAttribute('data-active');
                            }
                        }
                        btn.setAttribute('data-active', '');
                        for (let otherBtn of btns) {
                            if (parseInt(otherBtn.getAttribute('data-tab')) !== id) {
                                otherBtn.removeAttribute('data-active');
                            }
                        }
                    });
                }
            });
        }

        const doProportionIcon = (icons) => {
            for (const icon of icons) {
                const widthAttr = icon.getAttribute('width');
                const widthReal = icon.clientWidth;
                const heightAttr = icon.getAttribute('height');
                const heightReal = (widthReal / widthAttr) * heightAttr;
                icon.style.height = `${heightReal}px`;
            }
        }


        const icons = document.querySelectorAll('.icon-pr');
        if (icons.length) {
            doProportionIcon(icons);
            window.addEventListener('resize', debounce(() => {
                doProportionIcon(icons);
            }, 300));
        }
    }
);